import { GiMailedFist, GiPocketBow, GiHeartPlus, GiCheckedShield, GiFlyingFlag } from 'react-icons/gi';
import { keywordDatabase } from '../databases/Keywords';
const iconStyle = { marginBottom: '0.2em' };

const Spellbook = ({ unit }) => {
  return (
    <>
      <li className='list-group-item'>
        <small>
          <strong>Spellbook.</strong> This character can cast spells from the following list.
        </small>
        <small>
          <table className='table table-striped table-sm'>
            <thead>
              <tr>
                <th>Cast</th>
                <th className='text-muted'>Spell List</th>
              </tr>
            </thead>
            <tbody>
              {unit.rules
                ? unit.rules.map((e) =>
                    keywordDatabase.spells.map((f) =>
                      f.name === e ? (
                        <tr>
                          <th>{f.cast}+</th>
                          <td>
                            <strong>{f.name}.</strong> {f.text}
                          </td>
                        </tr>
                      ) : (
                        ''
                      )
                    )
                  )
                : ''}
            </tbody>
          </table>
        </small>
      </li>
    </>
  );
};
export default Spellbook;
